export default{
	data(){
		return{
			id:'',
			state:'',
			storeone:false,
			storetwo:false,
			storethree:false,
			storefour:false,
			windowHeight:'',
		}
	},
	onLoad() {
		var that=this;
		that.windowHeight = this.statusBarHeight+44;
		uni.getStorage({
			key:'userinfo',
			success:function(res) {
				that.id=res.data.id;
				that.getinfo();
			}
		})
	},
	methods: {
		getinfo:function(){
			var that=this;
			uni.request({
				url: that.serveipd+'/api/merchant/auth/info/'+that.id,
				method: 'GET',
				data: {
					id:that.id
				},
				success: res => {
					that.state=res.data.data.state;
					switch (that.state){
						case 1:
							that.storeone=true;
							console.log(that.storeone);
							that.storetwo=false;
							that.storethree=false;
							that.storefour=false;
							break;
						case 2:
						console.log('22');
							that.storeone=false;
							that.storetwo=true;
							that.storethree=false;
							that.storefour=false;
						   break;
						case 3:
						console.log('33');
							that.storeone=false;
							that.storetwo=false;
							that.storethree=true;
							that.storefour=false;
						   break;
						case 4:
						console.log('44');
							that.storeone=false;
							that.storetwo=false;
							that.storethree=false;
							that.storefour=true;
						   break;
						default:
						console.log('aaaaaa');
							break;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		back:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		switch1Change:function(e){
			var that=this;
			if(e.target.value == true){
				that.storeone=true;
				that.storetwo=false;
				that.storethree=false;
				that.storefour=false;
				that.state=1;
			}else{
				that.storeone=false;
				that.storetwo=true;
				that.state=2;
			}
			uni.request({
				url:that.serveipd+"/api/merchant/auth/code",
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				data: {
					statusCode:parseInt(that.state),
					id:parseInt(that.id),
				},
				success: res => {
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		switch3Change:function(e){
			var that=this;
			if(e.target.value == true){
			 	that.storeone=false;
			 	that.storetwo=false;
			 	that.storethree=true;
			 	that.storefour=false;
				that.state=3;
			}else{
				that.storethree=false;
				that.storetwo=true;
				that.state=2;
			}
			uni.request({
				url:that.serveipd+"/api/merchant/auth/code",
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				data: {
					statusCode:parseInt(that.state),
					id:parseInt(that.id),
				},
				success: res => {
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		switch4Change:function(e){
			var that=this;
			if(e.target.value == true){
			  	that.storeone=false;
			  	that.storetwo=false;
			  	that.storethree=false;
			  	that.storefour=true;
				that.state=4;
			}else{
				that.storethree=false;
				that.storetwo=true;
				that.state=2;
			}
			uni.request({
				url:that.serveipd+"/api/merchant/auth/code",
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				data: {
					statusCode:parseInt(that.state),
					id:parseInt(that.id),
				},
				success: res => {
				},
				fail: () => {},
				complete: () => {}
			});
		},
	},
}
